.about {
  background: url("../../img/index-background-mobile.png") no-repeat 0 0;
  background-size: auto;

  color: #ffffff;

  .about__wrapper {
    width: 280px;
    padding: 0 20px;
    
  }
  
  h2 {    
    line-height: 36px;
    color: #ffffff;
  }

  p {    
    font-size: 14px;
    line-height: 14px;
    font-weight: 300;
    text-transform: uppercase;
  }

  a {
    display: block;
    width: 280px;
    height: 40px;
    

    font-size: 14px;
    line-height: 18px;
    font-weight: 300;
    text-transform: uppercase;

    color: #ffffff;
    background-color: #68b738;
  }
}

$tablet-width: 768px;
$desktop-width: 1440px;

@media (min-width: $tablet-width) {
  
  .about .about__wrapper {
    width: 708px;
    padding: 0 30px;
    margin: 0 auto;
  }
}

